const searchTextField = document.getElementById('search-text-field');
const searchButton = document.getElementById('search-button');
const carousels = document.querySelectorAll('.carousel-content');
const carouselContainer = document.querySelector('.carousel-inner');

searchTextField.addEventListener('keyup', (e) => {
  if (e.key === 'Enter') {
    // do something
    console.log(searchTextField.value);
    searchTextField.value = '';
    carousels.forEach((element) => {
      element.classList.add('carousel-content-colapse');
    });
    carouselContainer.classList.add('carousel-inner-colapse');
  }
});

searchButton.addEventListener('click', (e) => {
  console.log(searchTextField.value);
  searchTextField.value = '';
});
